body {
  background-color: #3a3b3f;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.container__page {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
}

.btn__main {
  position: absolute;
  display: block;
  height: 40px;
  width: 120px;
  padding-top: 15px;
  border-radius: 3px;
  background: linear-gradient(45deg, #7117a5, blue);
  box-shadow: 2px 3px 4px #0000007e;
  text-decoration: none;
  overflow: hidden;
}
.btn__main a {
  text-decoration: none;
}
.btn__main a span {
  height: 40px;
  width: 120px;
  overflow: hidden;
  padding: 20px 40px 20px 40px;
  font-size: 20px;
  color: #fff;
}
.btn__main:hover {
  background: linear-gradient(25deg, #7817a5, blue);
  box-shadow: 2px 3px 3px #00000055;
}

.btn__lnik {
  height: 100%;
  width: 100%;
}

.btn__position {
  width: 100%;
  height: 400px;
  padding-top: 70px;
  display: flex;
  justify-content: center;
}

.modal__main {
  height: -webkit-max-content;
  height: max-content;
  height: -moz-fit-content;
  width: -webkit-max-content;
  width: max-content;
  width: -moz-fit-content;
  border-radius: 3px;
  background-color: #ffffff;
  box-shadow: 3px 3px 4px #000002c4;
  margin-top: 20%;
  z-index: 1;
  display: none;
  position: fixed;
}

.modal__title {
  height: 60px;
  background: linear-gradient(45deg, #7117a5, blue);
  padding: 2px;
  color: #ffffff;
  font-size: 20px;
  padding-top: 10px;
}
.modal__title > div {
  float: left;
}

.modal__text__title {
  width: 88%;
  padding: 20px 0 0 20px;
}

.modal__close {
  display: flex;
  justify-content: center;
  height: 50px;
  width: 50px;
  background-color: #941010;
  box-shadow: 2px 2px 3px #00000073;
  font-size: 30px;
  cursor: pointer;
}

.modal__text {
  height: auto;
  background: #ffffff;
  padding: 15px;
}

.modal__footer {
  height: 30px;
  background: linear-gradient(45deg, #7117a5, blue);
}

.modal__close__line, .modal__close__line1 {
  height: 4px;
  width: 35px;
  background-color: #ffffff;
  position: fixed;
  z-index: 2;
  margin-top: 25px;
  -o-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
}

.modal__close__line1 {
  -o-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}